/**
 * JavaScript Test Page
 * 
 * JavaScript for the test page functionality.
 */

// Import components for testing
import Navigation from '../components/navigation';
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';
import ProgressBar from '../components/progress-bar';
import ImageOptimizer from '../components/image-optimizer';
import { initWebpDetection } from '../utils/webp-detection';

/**
 * Initialize all components for testing
 */
function initComponents() {
  console.log('Initializing components for testing');
  
  // Initialize components
  if (Navigation) {
    Navigation.init();
    console.log('Navigation component initialized');
  }
  
  if (Accordion) {
    Accordion.init({
      triggerSelector: '.faq-question',
      contentSelector: '.faq-answer',
      closeOthers: true,
      animateHeight: true
    });
    console.log('Accordion component initialized');
  }
  
  if (Testimonials) {
    Testimonials.init({
      testimonialSelector: '.testimonial',
      dotSelector: '.testimonial-dot',
      activeClass: 'active',
      autoRotate: true,
      autoRotateInterval: 5000
    });
    console.log('Testimonials component initialized');
  }
  
  if (ProgressBar) {
    ProgressBar.init();
    console.log('ProgressBar component initialized');
  }
  
  if (ImageOptimizer) {
    ImageOptimizer.init({
      imageSelector: 'img:not([loading])',
      aboveTheFoldPriority: 'high',
      belowTheFoldPriority: 'low',
      addDecoding: true
    });
    console.log('ImageOptimizer component initialized');
  }
  
  // Initialize WebP detection
  initWebpDetection();
}

/**
 * Set up progress bar test buttons
 */
function setupProgressButtons() {
  function setupProgressButton(buttonId, step) {
    const button = document.getElementById(buttonId);
    if (button) {
      button.addEventListener('click', function() {
        if (ProgressBar && ProgressBar.setCurrentStep) {
          try {
            ProgressBar.setCurrentStep(step);
            console.log('Progress set to Step ' + step);
            
            // Update the display in our test section
            const displayElement = document.getElementById('current-progress-display');
            if (displayElement) {
              displayElement.textContent = step;
            }
          } catch (error) {
            console.error('Error setting progress:', error);
            alert('Error setting progress: ' + error.message);
          }
        } else {
          console.error('ProgressBar module not found or setCurrentStep method not available');
          console.dir(ProgressBar);
          alert('Error: ProgressBar module not found or setCurrentStep method not available. Check console for details.');
        }
      });
    }
  }
  
  // Setup all progress test buttons
  setupProgressButton('test-progress-btn-1', 1);
  setupProgressButton('test-progress-btn-3', 3);
  setupProgressButton('test-progress-btn-5', 5);
  setupProgressButton('test-progress-btn-9', 9);
  
  // Update the current progress display
  const currentProgressDisplay = document.getElementById('current-progress-display');
  if (currentProgressDisplay && ProgressBar && ProgressBar.getCurrentStep) {
    try {
      const currentStep = ProgressBar.getCurrentStep();
      currentProgressDisplay.textContent = currentStep;
    } catch (error) {
      console.error('Error getting current step:', error);
    }
  }
}

/**
 * Set up debug controls
 */
function setupDebugControls() {
  const debugOutput = document.getElementById('debug-output');
  
  function updateDebugOutput(message) {
    if (debugOutput) {
      const timestamp = new Date().toLocaleTimeString();
      debugOutput.innerHTML += `[${timestamp}] ${message}\n`;
      debugOutput.scrollTop = debugOutput.scrollHeight;
    }
  }
  
  // Check Elements button
  const checkElementsBtn = document.getElementById('debug-check-elements');
  if (checkElementsBtn) {
    checkElementsBtn.addEventListener('click', function() {
      if (window.DebugProgressBar) {
        const elements = window.DebugProgressBar.checkElements();
        updateDebugOutput('Elements checked. See console for details.');
        
        // Display element info in debug output
        for (const [key, value] of Object.entries(elements)) {
          updateDebugOutput(`${key}: ${value ? 'Found' : 'Not found'}`);
        }
      } else {
        updateDebugOutput('Debug Progress Bar not available');
      }
    });
  }
  
  // Get Stored Step button
  const getStoredStepBtn = document.getElementById('debug-get-stored-step');
  if (getStoredStepBtn) {
    getStoredStepBtn.addEventListener('click', function() {
      if (window.DebugProgressBar) {
        const step = window.DebugProgressBar.getStoredStep();
        updateDebugOutput(`Stored step: ${step}`);
      } else {
        updateDebugOutput('Debug Progress Bar not available');
      }
    });
  }
  
  // Init Progress Bar button
  const initProgressBtn = document.getElementById('debug-init-progress');
  if (initProgressBtn) {
    initProgressBtn.addEventListener('click', function() {
      if (window.DebugProgressBar) {
        const result = window.DebugProgressBar.initProgressBar();
        updateDebugOutput(`Progress bar initialization: ${result ? 'Success' : 'Failed'}`);
      } else {
        updateDebugOutput('Debug Progress Bar not available');
      }
    });
  }
  
  // Direct Update UI button
  const directUpdateBtn = document.getElementById('debug-direct-update');
  if (directUpdateBtn) {
    directUpdateBtn.addEventListener('click', function() {
      if (window.DebugProgressBar) {
        const step = prompt('Enter step (1-9):', '3');
        if (step) {
          const result = window.DebugProgressBar.setStep(parseInt(step, 10));
          updateDebugOutput(`Direct UI update to step ${step}: ${result ? 'Success' : 'Failed'}`);
        }
      } else {
        updateDebugOutput('Debug Progress Bar not available');
      }
    });
  }
}

/**
 * Set up WebP detection test
 */
function setupWebPDetection() {
  const webpStatusElement = document.getElementById('webp-support-status');
  if (webpStatusElement) {
    if (document.documentElement.classList.contains('webp')) {
      webpStatusElement.textContent = 'DOES';
      webpStatusElement.style.color = 'green';
    } else if (document.documentElement.classList.contains('no-webp')) {
      webpStatusElement.textContent = 'DOES NOT';
      webpStatusElement.style.color = 'red';
    } else {
      webpStatusElement.textContent = 'UNKNOWN';
      webpStatusElement.style.color = 'orange';
    }
  }
}

/**
 * Initialize the test page
 */
function initTestPage() {
  console.log('Initializing JS Test Page');
  
  // Initialize components
  initComponents();
  
  // Set up progress buttons
  setupProgressButtons();
  
  // Set up debug controls
  setupDebugControls();
  
  // Set up WebP detection test
  setupWebPDetection();
}

// Initialize when DOM is ready
document.addEventListener('DOMContentLoaded', initTestPage);

export default {
  initTestPage
};
